import React, { useState } from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  const [navToggled, setNavToggled] = useState(false);

  const toggleNav = el => {
    if (!el.classList.value.includes("toggled")) {
      setNavToggled(true);
      el.classList.add("toggled");
    } else {
      setNavToggled(false);
      el.classList.remove("toggled");
    }
  };

  return (
    <nav className="navbar">
      <img className="nav__logo" src="./files/img/logo.png" alt="logo" />
      <ul className="nav__list">
        <li className="nav__list__item">
          <Link to="/">Home</Link>
        </li>
        <li className="nav__list__item">
          <Link to="/projects">Projects</Link>
        </li>
      </ul>
      <button
        className="nav-toggle-btn"
        onClick={e => {
          if (e.target.classList.value.includes("nav-toggle-btn")) {
            toggleNav(e.target);
          } else {
            toggleNav(e.target.parentElement);
          }
        }}
      >
        <div className="top"></div>
        <div className="middle"></div>
        <div className="bottom"></div>
      </button>
      {/* {navToggled && ( */}
      <ul
        className={
          navToggled
            ? "nav-toggle-list nav-toggled box-shadow"
            : "nav-toggle-list box-shadow"
        }
      >
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/projects">Projects</Link>
        </li>
      </ul>
      {/* )} */}
    </nav>
  );
};

export default Navbar;
