import React, { useState } from "react";

const Info = () => {
  const [about, setAbout] = useState(true);
  const [contact, setContact] = useState(false);

  const toggleBtn = e => {
    if (e.target.classList.value.includes("about-btn")) {
      setAbout(true);
      setContact(false);
    } else {
      setContact(true);
      setAbout(false);
    }
  };

  return (
    <div className="home-info">
      <div className="home-info__buttons">
        <button
          className={about ? "about-btn active" : "about-btn"}
          onClick={e => {
            toggleBtn(e);
          }}
        >
          About Me
        </button>
        <button
          className={contact ? "contact-btn active" : "contact-btn"}
          onClick={e => {
            toggleBtn(e);
          }}
        >
          Contact Me
        </button>
      </div>
      {about ? (
        <div className="home-info__about-me">
          {/* <div className="title">
            Benim <span>Hakkımda</span>
          </div>
          Merhaba! Ben Can, 23 yaşında ve yolun henüz başında bir yazılımcıyım.
          Zonguldak'ta doğdum, büyüdüm ve hala yaşıyorum. Kendimi planlı ve
          yaratıcı biri olarak görüyorum. Filmlerin takıntım olduğunu söylesem
          yalan söylememiş olurdum. Çocukluğumda çizmeyi çok sevmeme rağmen
          artık benim için çok da fazla yapmadığım bir hobi haline geldi. Bir
          yıldan fazladır web programlama yeteneklerimi geliştirmeye çalışıyorum
          ve daha fazla öğrenmeyi iple çekiyorum. Buradan{" "}
          <a href="./files/mca_cv_en.pdf" target="_blank">
            özgeçmişimi
          </a>{" "}
          görüntüleyebilirsiniz. */}
          <div className="title">
            About <span>Me</span>
          </div>
          Hi! I'm Can, a 23 years old developer who's just at the beginning of
          his journey. I was born, raised and currently living in Zonguldak. I
          consider myself as an organized, and a creative person. I wouldn't be
          lying if I said movies were my obsession. Although I liked drawing
          very much during my childhood, it became a hobby that I don't do much
          nowadays. I've been trying to improve my web development skills for
          more than a year, and looking forward to learn more. You can see my{" "}
          <a href="./files/mca_cv_en.pdf" target="_blank">
            CV
          </a>{" "}
          here.
        </div>
      ) : (
        <div className="home-info__contact-me">
          {/* <div className="title">
            Bana <span>Ulaşın</span>
          </div> */}
          <div className="title">
            Contact <span>Me</span>
          </div>
          <div className="links">
            <a href="mailto:mca199667@gmail.com">
              <i className="fas fa-envelope"></i>
            </a>
            <a href="https://github.com/mehmetcanaygun">
              <i className="fab fa-github"></i>
            </a>
            <a href="https://www.linkedin.com/in/mehmetcanaygun/">
              <i className="fab fa-linkedin-in"></i>
            </a>
            <a href="https://twitter.com/mehmetcnaygun">
              <i className="fab fa-twitter"></i>
            </a>
            <a href="https://www.instagram.com/mehmetcnaygun/">
              <i className="fab fa-instagram"></i>
            </a>
          </div>
        </div>
      )}
    </div>
  );
};

export default Info;
