import React from "react";

const NotFound = () => {
  return (
    <div
      className="not-found-page"
      style={{ backgroundImage: `url(./files/img/notfound-bg.jpg)` }}
    >
      <div>
        <p>SORRY!</p>
        The page you're looking for was not found.
      </div>
    </div>
  );
};

export default NotFound;
